const RectanglePath = ({ bottomLeftX, bottomLeftY, topRightX, topRightY, radiusTopLeft, radiusTopRight, radiusBottomRight, radiusBottomLeft, fill }) => {
    const width = topRightX - bottomLeftX;
    const height = topRightY - bottomLeftY;
    const rTL = radiusTopLeft || 0;
    const rTR = radiusTopRight || 0;
    const rBR = radiusBottomRight || 0;
    const rBL = radiusBottomLeft || 0;

    const d = `
    M ${bottomLeftX + rBL},${bottomLeftY}
    H ${topRightX - rBR}
    Q ${topRightX},${bottomLeftY} ${topRightX},${bottomLeftY + rBR}
    V ${topRightY - rTR}
    Q ${topRightX},${topRightY} ${topRightX - rTR},${topRightY}
    H ${bottomLeftX + rTL}
    Q ${bottomLeftX},${topRightY} ${bottomLeftX},${topRightY - rTL}
    V ${bottomLeftY + rBL}
    Q ${bottomLeftX},${bottomLeftY} ${bottomLeftX + rBL},${bottomLeftY}
    Z
  `;
  
    return (
      <path d={d} fill={fill || '#000'} />
    );
};
